"use client";

import React, { useState, useEffect } from "react";
import { filesData, type FileItem } from "@/lib/data";
import { FileDisplay } from "@/components/ui/FileAccess";
import { EmptyState } from "@/components/ui/EmptyState";
import { useView } from "@/context/ViewContext";
import { FileActionModal } from "@/components/ui/FileActionModal";
import { ContextMenu } from "@/components/ui/ContextMenu";

export default function BerandaPage() {
  const { viewMode } = useView();
  const [isLoading, setIsLoading] = useState(true);
  const [selectedFile, setSelectedFile] = useState<FileItem | null>(null);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [contextMenu, setContextMenu] = useState<{
    position: { x: number; y: number };
    file: FileItem;
  } | null>(null);

  // Pisahkan data untuk file dan folder
  const folders = filesData.filter((item) => item.type === "folder");
  const files = filesData.filter((item) => item.type === "file");
  const hasContent = filesData.length > 0;

  useEffect(() => {
    const timer = setTimeout(() => setIsLoading(false), 1500);
    return () => clearTimeout(timer);
  }, []);

  // Handler untuk klik file
  const handleFileClick = (file: FileItem) => {
    if (file.type === "file") {
      setSelectedFile(file);
      setIsModalOpen(true);
    } else {
      // Menghindari penggunaan `alert` untuk pengalaman pengguna yang lebih baik
      console.log(`Membuka folder: ${file.name}`);
    }
  };

  // Handler untuk menutup modal
  const handleCloseModal = () => {
    setIsModalOpen(false);
    setTimeout(() => setSelectedFile(null), 300);
  };

  // Handler untuk event context menu
  const handleFileContextMenu = (
    event: React.MouseEvent,
    file: FileItem
  ) => {
    event.preventDefault();
    setContextMenu({
      position: { x: event.pageX, y: event.pageY },
      file,
    });
  };

  // Handler untuk menutup context menu
  const handleCloseContextMenu = () => {
    setContextMenu(null);
  };

  return (
    <div onClick={handleCloseContextMenu} onContextMenu={handleCloseContextMenu}>
      <h1 className="text-3xl font-bold">Beranda</h1>

      {!hasContent && !isLoading ? (
        <div className="mt-8">
          <EmptyState />
        </div>
      ) : (
        <>
          <FileDisplay
            title="Folder"
            items={folders}
            viewMode={viewMode}
            isLoading={isLoading}
            onFileClick={handleFileClick}
            onFileContextMenu={handleFileContextMenu}
          />
          <FileDisplay
            title="File"
            items={files}
            viewMode={viewMode}
            isLoading={isLoading}
            onFileClick={handleFileClick}
            onFileContextMenu={handleFileContextMenu}
          />
        </>
      )}

      <FileActionModal
        isOpen={isModalOpen}
        onClose={handleCloseModal}
        file={selectedFile}
      />
      {contextMenu && (
        <ContextMenu
          isOpen={true}
          onClose={handleCloseContextMenu}
          position={contextMenu.position}
          file={contextMenu.file}
        />
      )}
    </div>
  );
}